<template>
  <div id="login">
    <div v-if="!this.$parent.authenticated">
      <button
        v-on:click="login()"
      >
      Login
      </button>
    </div>
  </div>
</template>

<script>

const PATH_TO_PROTECTED_ROUTE = '/menu'
export default {
  name: 'login',
  methods: {
    login () {
      this.$auth.signInWithRedirect({ originalUri: PATH_TO_PROTECTED_ROUTE })
    },
  }
}
</script>